import axios from 'axios';
import React, { useRef, useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import ReactToPrint from 'react-to-print-advanced';
import WaterApiList from '../../../../Components/ApiList/WaterApiList';
import CitizenApplyApiList from '../../../../Components/CitizenApplyApiList';
import BarLoader from '../../../../Components/Common/BarLoader';
import ComponentToPrint from './WaterConnPayReceipt';


function WaterConnectionPayReceiptIndex() {
    const [loader, setLoader] = useState(false)

    const { paymentId } = useParams()
    const { api_paymentReceipt, header } = WaterApiList();

    const componentRef = useRef();
    const [show, setshow] = useState(false)
    const [receiptData, setReceiptData] = useState()


    useEffect(() => {
        setLoader(true)
        axios.post(api_paymentReceipt, { "transactionNo": paymentId }, header)
            .then((res) => {
                setLoader(false)
                console.log("Receipt Response", res.data.data)
                setReceiptData(res.data.data)
            })
            .catch((err) => {
                console.log("Error while fetcking receipt DATA", err)
                setLoader(false)
            })
    }, [paymentId])


    const showLoader = (val) => {
        setshow(val);
    }

    return (
        <div>
            {loader && <BarLoader />}
            <ReactToPrint
                trigger={() => <button></button>}
                content={() => componentRef.current}
            />
            <ComponentToPrint ref={componentRef} receiptData={receiptData} />
        </div>
    )
}

export default WaterConnectionPayReceiptIndex